import { IconAbc, IconBuilding, IconCar, IconParking } from '@tabler/icons-react';
import { Image } from 'antd';
import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom';
import { getVehicles } from '../../../API/CustomerDetails';

const VehicleInfo = () => {
    const [vehicledata, setVehicleData] = useState([]);
    const location = useLocation();
    console.log(location.state)
    const navigate = useNavigate();
    console.log(location)
    const token = localStorage.getItem('admin_token')


    const vehicle_listById = async () => {
        try {
            let total = await getVehicles(token, location.state)
            console.log('getVehicles', total);
            setVehicleData(total.data)
        } catch (error) {
            console.log('getVehicles', error)
        }
    }

    useEffect(() => {
        vehicle_listById();
    }, [])
    return (
        <div className="page-body">
            <div className="container-xl">
                <div className="card">
                    {/* <div className="card-header">
                            <div><h3 style={{ float: 'left' }}>Vehicle info</h3></div>
                        </div> */}

                    {vehicledata.length == 0 &&
                        <div className='text-center p-4'>
                            <img src='defaultcar.png' width={200} height={90} />
                            <h4 style={{ fontWeight: '300' }}>No Vehicle Added Yet!</h4>
                        </div>
                    }
                    {vehicledata.map((item, i) => {
                        return (
                            <div className="card-body " style={{ textTransform: 'capitalize' }} key={i}>
                                <div className='row' style={{ display: 'inline' }}>
                                    <div className='col-lg-2' style={{ display: 'inline' }}>
                                        {item.image == null ?
                                            <Image src='bluescarwash.png' width={140} height={140} className="profileimg" />
                                            :
                                            <Image src={item.image} width={140} height={73} className="profileimg" />
                                        }
                                    </div>
                                    <div className='col-lg-10' style={{ float: 'left' }}>
                                        <div className="datagrid">
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><i className='fas fa-car' style={{ width: '20px' }}></i></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Vehicle Body-Type</div>
                                                    <div className='font-weight-medium'>
                                                        {item.vehicle_title}</div>
                                                </div>
                                            </div>
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><i className='fas fa-car-side' style={{ width: '20px' }}></i></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Vehicle Model</div>
                                                    <div className='font-weight-medium'>
                                                        {item.vehicle_model}</div>
                                                </div>
                                            </div>
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><i className='fas fa-palette' style={{ width: '20px' }}></i></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Vehicle Color</div>
                                                    <div className='font-weight-medium'>
                                                        {item.vehicle_color}</div>
                                                </div>
                                            </div>
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><IconAbc /></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Vehicle Number</div>
                                                    <div className='font-weight-medium'>
                                                        {item.license_num}</div>
                                                </div>
                                            </div>
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><IconCar /></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Vehicle Type</div>
                                                    <div className='font-weight-medium'>
                                                        Four Wheeler</div>
                                                </div>
                                            </div>
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><IconBuilding /></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Basement Number</div>
                                                    <div className='font-weight-medium'>
                                                        {item.basement_no}</div>
                                                </div>
                                            </div>
                                            <div className="row align-items-center">
                                                <div className="col-auto"><span className='bg-primary text-white avatar'><IconParking /></span></div>
                                                <div className="col">
                                                    <div className='text-muted' style={{ fontSize: '11px' }}>Parking Lot Number</div>
                                                    <div className='font-weight-medium'>
                                                        {item.parking_num}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

export default VehicleInfo
