import axios from "axios"
import BASE_URL from "../Config"

const getVehicles = async (token,id) => {
    try {
        const res = await axios(`${BASE_URL}get-customer-vehicle-list/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const getCustomerDetails = async (token, id) => {
    try {
        const res = await axios.get(`${BASE_URL}getCustomer/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const viewTaskDetailsData = async (token, id) => {
    try {
        const res = await axios.get(`${BASE_URL}completeTaskListByServiceId/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getVehicles, getCustomerDetails ,viewTaskDetailsData }