import React, { useEffect } from 'react'
import { Tab, Tabs } from 'react-bootstrap'
import { Helmet } from 'react-helmet'
import { useLocation } from 'react-router-dom'
import { viewTaskDetailsData } from '../../API/ViewTaskDetails'
import Container from '../../Component/Container'
import AfterImage from './DetailsTabs/AfterImage'
import BeforeImage from './DetailsTabs/BeforeImage'
import Complaint from './DetailsTabs/Complaint'
import ServiceInformation from './DetailsTabs/ServiceInformation'
import Washer from './DetailsTabs/Washer'

const ViewDetails = () => {
  const location = useLocation();
  const token = localStorage.getItem('admin_token')
  console.log(location);

  const viewAssignTaskDetails = async () => {
    try {
      const total = await viewTaskDetailsData(token, location.state)
      console.log("viewTaskDetailsData", total)
    } catch (error) {
      console.log("viewTaskDetailsData", error)
    }
  }

  useEffect(() => {
    viewAssignTaskDetails();
  }, [])

  return (
    <Container>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | View Task Details</title>
      </Helmet>
      <div className="page-wrapper">
        <div className="page-header d-print-none">
          <div className="container-xl">
            <div className="row g-2 align-items-center">
              <div className="col">
                <h2 className="page-title" style={{ float: 'left' }}>
                  View Task Details
                </h2>
              </div>
            </div>
          </div>
        </div>
        <div className="page-body">
          <div className="container-xl">
            {/* <div className="card">
              <div className="card-header">
                <div><h3 style={{ float: 'left' }}>Base info</h3></div>
              </div>
            </div> */}
            <div style={{ width: "100%" }}>
              <div>
                <Tabs
                  defaultActiveKey="Before Images"
                  id="fill-tab-example"
                  className="mb-3"
                  fill
                >
                  <Tab eventKey="Before Images" title="Before Images">
                    <BeforeImage />
                  </Tab>
                  <Tab eventKey="After Images" title="After Images">
                    <AfterImage />
                  </Tab>
                  <Tab eventKey="Service Information" title="Service Information">
                    <ServiceInformation />
                  </Tab>
                  <Tab eventKey="Washer Details" title="Washer Details">
                    <Washer />
                  </Tab>
                  <Tab eventKey="Complaint" title="Complaint">
                    <Complaint />
                  </Tab>
                </Tabs>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Container >
  )
}

export default ViewDetails
